import React, {useState} from 'react';
import './main-page.css';
import Header from './header';
import Component_fruits from './component_fruits';
import Component_vegetables from './component_vegetables';
import Component_groceries from './component_groceries';

function App() {
    const [showFruits, setShowFruits] = useState(true);
    const [showVegetables, setShowVegetables] = useState(false);
    const [showGroceries, setShowGroceries] = useState(false);
    return (
        <div className="container">
            <Header header_subtitle="Fresh and Healthy"/>
            <hr/>
            <div>
                <div className="btn-toolbar" role="toolbar" aria-label="Toolbar with button groups">
                    <div className="btn-group mr-2" role="group" aria-label="First group">
                        <button className="button_products" type="button" class="btn btn-secondary" onClick={() => {
                            setShowFruits(true);
                            setShowVegetables(false);
                            setShowGroceries(false);

                        }}>
                            Fruits
                        </button>
                    </div>
                    <div className="btn-group mr-2" role="group" aria-label="Second group">
                        <button className="button_products" type="button" class="btn btn-secondary" onClick={() => {
                            setShowFruits(false);
                            setShowVegetables(true);
                            setShowGroceries(false);

                        }}>
                            Vegetables
                        </button>
                    </div>
                    <div className="btn-group" role="group" aria-label="Third group">
                        <button className="button_products" type="button" class="btn btn-secondary" onClick={() => {
                            setShowFruits(false);
                            setShowVegetables(false);
                            setShowGroceries(true);

                        }}>
                            Groceries
                        </button>
                    </div>
                </div>
            </div>
            <hr/>
            {/* eslint-disable-next-line react/jsx-pascal-case */}
            {showFruits && <Component_fruits/>}
            {/* eslint-disable-next-line react/jsx-pascal-case */}
            {showVegetables && <Component_vegetables/>}
            {/* eslint-disable-next-line react/jsx-pascal-case */}
            {showGroceries && <Component_groceries/>}

        </div>
    );
}

export default App;
